import React from 'react'
import * as FIcon from 'react-feather'
import {observer} from "mobx-react-lite"
import {useStores} from "../../models/root-store/root-store-context"
import './AdminPedidos.css'
import {Page, SDataTable} from "../../components"
import {RouteComponentProps} from "react-router";
import {RUTAS} from "../../constants";

const moment = require('moment-timezone')
const numeral = require("numeral")

export const AdminHojaRuta: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const DataTable = React.createRef<any>()

  const visualizar = (row: any) => {
    history.push(RUTAS.ADMIN_HOJA_RUTA_DETAIL(row.id))
  }

  const renderAcciones = (row: any) => {
    return <div>
      <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}><FIcon.Eye size={14}/></span>
    </div>
  }

  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Hojas de ruta</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Hojas de ruta</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  return rootStore.environment.api.hojasRuta(page, perPage, order, search)
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: true,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                  },
                  {
                    name: 'Tipo de vehculo',
                    selector: 'tipo_vehiculo',
                    sortable: true,
                    cell: (row: any) => row.tipo_vehiculo ? row.tipo_vehiculo.nombre : 'NO ASIGNADO'
                  },
                  {
                    name: 'Cantidad de paradas',
                    selector: 'cantidad_paradas',
                    sortable: true,
                  },
                  {
                    name: 'Cantidad de candidatos',
                    selector: 'cantidad_candidatos',
                    sortable: true,
                  },
                  {
                    name: 'Transportista',
                    selector: 'transportista',
                    sortable: true,
                    cell: (row: any) => {
                      if (row.transportista) {
                        return (row.transportista.usuario.first_name + ' ' + row.transportista.usuario.last_name)
                      } else {
                        return (
                          'NO ASIGNADO'
                        )
                      }
                    }
                  },
                  {
                    name: 'Monto a cobrar',
                    selector: 'tarifa_transportista',
                    sortable: true,
                    cell: (row: any) => 'Gs. ' + numeral(row.tarifa_transportista).format("0,0")
                  },
                  {
                    name: 'Finalizado',
                    selector: 'finalizado',
                    sortable: true,
                    cell: (row: any) => row.finalizado ? 'S' : 'No',
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: renderAcciones,
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminHojaRuta;
